@-webkit-keyframes grow {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
    border-radius: 50%;
  }

 to {
   -webkit-transform: scale(1);
           transform: scale(1);
   border-radius: 0;
 }
}

@keyframes grow {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
    border-radius: 50%;
  }

 to {
   -webkit-transform: scale(1);
           transform: scale(1);
   border-radius: 0;
 }
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .9);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  z-index: 10;
}

.modal__card {
  z-index: 11;
  border-radius: 5px;
  padding: 30px;
  background-color: #FFFFFF;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-animation: grow .3s;
          animation: grow .3s;
  position: relative;
}

.modal__x {
  position: absolute;
  top: 5%;
  right: 10%;
  cursor: pointer;
}

